<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="保险种类">
                    <el-select v-model="filters.dict_id" placeholder="请选择">
                        <el-option
                            v-for="item in insuranceTypeOptions"
                            :key="item.type_item"
                            :label="item.type_item_name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="保险公司">
                    <el-select v-model="filters.company_id" placeholder="请选择">
                        <el-option
                            v-for="item in insuranceCompanyOptions"
                            :key="item.company_id"
                            :label="item.company_name"
                            :value="item.company_id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-button @click="handleSearch">查询</el-button>
                <el-button type="primary" icon="plus" @click="$router.push({ path: '/productSupplier/add'})">新建</el-button>
            </el-form>
            <el-table :data="tableData" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="company_name" label="保险公司" width="120">
                </el-table-column>
                <el-table-column prop="insurance_name" label="保险名称" width="120">
                </el-table-column>
                <el-table-column prop="dict_type_name" label="保险种类" width="120">
                </el-table-column>
                <el-table-column prop="insurance_price" label="保险价格">
                </el-table-column>
                <el-table-column prop="status_name" label="是否启用">
                </el-table-column>
                </el-table-column>
                <el-table-column prop="create_time" label="创建时间">
                </el-table-column>
                </el-table-column>
                <el-table-column prop="create_user" label="创建人">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-view" @click="$router.push({ path: '/productSupplier/view', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">查看</el-button>
                        <el-button type="text" icon="el-icon-edit" @click="$router.push({ path: '/productSupplier/edit', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">修改</el-button>

                        <el-button type="text" icon="el-icon-delete" @click="handleStatus(row, 3)" v-if="row.status == 1">禁用</el-button>
                        <el-button type="text" icon="el-icon-delete" @click="handleStatus(row, 1)" v-else>启用</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import * as API from '@/api/resourceManagement'
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                total: 1,
                limit: 10,
                filters: {
                   
                },
                insuranceTypeOptions: [],
                insuranceCompanyOptions: [],
            }
        },
        created() {
            this.getData();
            this.getInsuranceType()
            this.getInsuranceCompany()
        },
        computed: {
            
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params = {
                    page: this.page,
                    ...this.filters
                }
                API.apiProductSupplierList(params).then(res => {
                    if(res.code == 0 && res.data){
                        this.tableData = res.data;
                        this.total = res.total;
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getInsuranceType(){
                API.apiProductSupplierType({}).then(res => {
                    if(res.code == 0 && res.data){
                        this.insuranceTypeOptions = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getInsuranceCompany(){
                API.apiProductSupplierCompany({}).then(res => {
                    if(res.code == 0 && res.data){
                        this.insuranceCompanyOptions = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSearch() {
                this.getData();
            },
            handleDelete(row, index) {
                
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            handleStatus(row, num){
                let params = {
                    id: row.id,
                    operation_uniqid: row.operation_uniqid,
                    status: num
                }
                API.apiProductSupplierEdit(params).then(res => {
                    if(res.code == 0 && res.data){
                        this.getData();
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
